:root {
  font-family: Roboto, sans-serif;
}

body {
  letter-spacing: .1px;
  font-family: Roboto, sans-serif;
  line-height: 1.15;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, ul, ol, p {
  margin: 0;
  padding: 0;
}

a, a:visited {
  color: currentColor;
  text-decoration: none;
}

figcaption {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  display: block;
}

.container {
  max-width: 480px;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 75px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding: 0 70px;
  }
}

.footer {
  color: #545454;
  background-color: #f7f7f7;
}

